@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&family=Poppins:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    overflow-x: hidden;
}
.switch-wrapper {
    position: relative;
  }
.switch-wrapper > .body_wrapper {
    position: absolute;
}
.switch-wrapper > .loginPage {
    position: absolute;
}


  a {
    color: #000;
    text-decoration: none;
    font-size: 18px;
}

.line {
  background-color: #4a4e69;
  height: 10vh;
  width: 100%;
}
.about_section {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 30vh;
  color: #797979;
  padding-top: 0px;
}

.about_section .about__left, .about_section .about__right {
  flex-basis: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
  padding-top: 0px;
}

.about_section .about__right img {
  width: 125%;
  height: 125%;
}

.about_section .about__left {
  margin-top: 80px;
  margin-bottom: 80px;
  flex-direction: column;
  text-align: left;
  justify-content: center;
  align-items: flex-start;
}

.about_section .about__left .about__one {
  color: #040c2c;
  font-family: 'Work Sans', sans-serif;
  padding: 0px;
  font-size: 40px;
  margin: 0px;
}

.about_section .about__left .about__two {
  font-size: 15px;
  color: #acacac;
  font-family: Montserrat,sans-serif;
  padding: 0px;
  margin: 0px;
}

.about_section .about__left .icons i {
  padding-left: 10px;
}

.about_section .about__left .about__buttons {
  font-family: Montserrat,sans-serif;
}

.about_section .about__left .about__buttons button {
  cursor: pointer;
  transition: 0.3s ease;
}

.about_section .about__left .about__buttons .btn_one {
  background-color: #040c2c;
  border: 1px solid #040c2c;
  border-radius: 3px;
  padding: 5px;
  color: white;
  padding: 10px;
  font-size: 20px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: 30px;
}

.about_section .about__left .about__buttons .btn_two {
  background-color: white;
  color: #040c2c;
  border: 1px solid #040c2c;
  border-radius: 3px;
  padding: 10px;
  padding-right: 20px;
  padding-left: 20px;
  font-size: 20px;
}

.about_section .about__left .about__buttons .btn_one:hover {
  background-color: white;
  color: #040c2c;
}

.about_section .about__left .about__buttons .btn_two:hover {
  background-color: #040c2c;
  color: white;
}
