.topNav {
  width: 100%;
  position: fixed;
  top: 0%;
}

.navbar {
  position: fixed;
  top: 0%;
  width: 100%;
  color: white;
  min-height: 5vh;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 10px;
  justify-content: center;
  align-items: center;
  font-size: 28px;
}

.navbar .topNav {
  padding-bottom: 20px;
  transition: 0.3s ease;
}

.navbar .mostBlack {
  background-color: #4a4e69;
}

.navbar div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.navbar div div {
  padding-top: 5px;
  z-index: 3;
  flex-basis: auto;
}

.navbar .icon {
  font-size: 55px;
  margin-right: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar .links {
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.navbar .links .navbar_links {
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  margin: 10px;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.navbar .links .navbar_links .nav_item {
  cursor: pointer;
  margin: 10px;
  font: 600 14px/30px Montserrat,sans-serif;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.navbar .blog {
  margin-left: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar .blog button {
  background-color: white;
  color: #4a4e69;
  cursor: pointer;
  padding: 5px 15px;
  font-size: 20px;
  border-radius: 5px;
  border: 1px solid white;
}

.navbar .mobile_navbar {
  font: 600 14px/30px Montserrat,sans-serif;
  width: 100%;
  margin: 0px;
  transition: 0.5s ease;
}

.navbar .mobile_navbar ul {
  display: flex;
  height: 100vh;
  flex-direction: column;
  list-style-type: none;
  justify-content: center;
  align-items: flex-start;
  z-index: 2;
  color: white;
  background-color: #4a4e69;
  transition: 0.3s ease;
  width: 100%;
  margin: 0px;
  padding-left: 0px;
}

.navbar .mobile_navbar ul li {
  cursor: pointer;
  padding-left: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.bars {
  color: white;
}

.hamburgers {
  position: fixed !important;
  width: 100vw;
  background-color: #4a4e69;
  z-index: 999;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.hamburgers div {
  margin-right: 5%;
  z-index: 999;
  color: white;
}

.fa-icon {
  position: fixed;
  color: white;
  transition: 0.3s ease;
}

.rotateIc {
  transform: rotate(90deg);
}

@media (max-width: 1000px) {
  .navbar div .icon {
    margin-right: 1px;
  }
  .navbar div .blog {
    margin-left: 1px;
  }
}

@media (min-width: 700px) {
  .hamburgers {
    display: none !important;
  }
  .mobile_navbar {
    transform: translateY(-200%);
  }
  .navbar {
    max-height: 20vh;
  }
}

@media (max-width: 700px) {
  .topNav {
    background-color: #4a4e69;
    opacity: 1;
    color: black;
    margin-bottom: 0px;
    padding-bottom: 0px !important;
  }
  .mobile_navbar_ul {
    transform: translateY(-200%);
    font-size: 20px;
  }
  .mobile_navbar {
    transform: translateY(-200%);
  }
  .navbar {
    width: 100%;
    transition: 0.5s ease;
    transform: translateY(-200vh);
  }
  .navbar .topNav {
    width: 100%;
  }
  .links {
    display: none !important;
  }
  .blog {
    display: none;
  }
  .blog button {
    display: none;
  }
  .icon {
    text-align: left;
    flex-basis: 50%;
  }
  .icon .first {
    justify-content: flex-start;
  }
  .icon div {
    display: flex;
    justify-content: flex-end;
  }
  .hamburger:hover {
    cursor: pointer;
  }
  .hamburger:hover .navbar .mobile_navbar {
    transition: 0.5s ease;
    text-align: left;
    display: flex;
  }
  .navUnHide {
    display: flex !important;
    width: 100%;
    transform: translateY(0%);
  }
  .navUnHide .mobile_navbar_ul {
    transform: translateY(0%);
  }
  .navbarUnHide {
    transform: translateY(0vh);
  }
}
/*# sourceMappingURL=nav.css.map */
.wrapper_image {
  z-index: 3;
  display: flex;
  background-position: 25% 75%;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 60px;
  margin: 0 !important;
  padding: 0 !important;
  min-height: 100vh;
  background-size: cover;
  overflow: hidden;
  filter: none;
  transform-style: preserve-3d;
}

.wrapper_image p {
  padding: 0px;
  margin: 0px;
  text-align: center;
}

.wrapper_image .name {
  font-size: 70px;
  font-family: 'Montserrat', sans-serif;
}

.wrapper_image .job {
  font-family: 'Poppins', sans-serif;
  font-size: 35px;
}

.wrapper_image .social_icons {
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper_image .social_icons i {
  background: rgba(0, 0, 0, 0.3);
  transition: 0.3s ease;
  color: white;
  padding: 10px;
  margin: 10px;
  border-radius: 50px;
  font-size: 25px;
}

.wrapper_image .social_icons i:hover {
  cursor: pointer;
  color: black;
  background-color: white;
}

.wrapper_image .welcome {
  font-size: 30px;
  margin-bottom: 20px;
  font-family: 'Raleway', sans-serif;
}

@media (max-width: 1000px) {
  .wrapper_image {
    background-position: 0% 100%;
  }
  .wrapper_image .name {
    font-size: 50px;
  }
  .wrapper_image .job {
    font-size: 30px;
  }
  .wrapper_image .social_icons {
    margin-bottom: 0px;
  }
}

@media (max-width: 700px) {
  .wrapper_image .name {
    font-size: 30px;
  }
  .wrapper_image .job {
    font-size: 25px;
  }
}
