.skills_section {
  background-color: #fafafa;
  padding: 100px;
}

.skills_section .skills_section_title {
  text-align: center;
  font-size: 50px;
  color: #fafafa;
  padding: 0px;
  margin: 0px;
}

.skills_section .skills_list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.skills_section .skills_list .skill {
  color: #8a8a8a;
  margin: 30px;
  flex-basis: 25%;
  background-color: #fff;
  border: 1px solid #f3f1f1;
  border-radius: 20px;
  padding: 10px;
  min-height: 20%;
}

.skills_section .skills_list .skill .skill__name {
  font-size: 70px;
  padding: 0px;
}

.skills_section .skills_list .skill .skill__name p {
  margin: 0px;
  margin-bottom: 20px;
}

.skills_section .skills_list .skill .count {
  margin-top: 0px;
  padding-top: 0px;
  display: flex;
  font-size: 23px;
}

.skills_section .skills_list .skill .count .count__project {
  margin: 0px;
  padding: 0px;
  color: #040c2c;
}

.skills_section .skills_list .skill .count .count__years {
  color: #4a4e69;
  margin: 0px;
  padding: 0px;
  margin-left: 10px;
}

.skills_section .skills_list .skill .count__blocks {
  display: flex;
}

.skills_section .skills_list .skill .count__blocks .block {
  width: 17px;
  height: 40px;
  padding: 0px;
  margin: 5px;
  border-radius: 10px;
}

.skills_section .skills_list .skill .count__blocks .projects {
  background-color: #040c2c;
}

.skills_section .skills_list .skill .count__blocks .years {
  background-color: #4a4e69;
}
